.message {
  text-align: center;
  width: 100%;
  height: 24px;
}

.win {
  color: #00b50e;
}

.perfect {
  font-size: 20px;
  font-weight: 900;
  color: #00b50e;
  -webkit-animation: blinker 1s linear infinite;
          animation: blinker 1s linear infinite;
}

@-webkit-keyframes blinker {
  50% {
    opacity: 0;
  }
}

@keyframes blinker {
  50% {
    opacity: 0;
  }
}

.fail {
  color: #db0202;
}



